export default {
  data() {
    return {
      username: '',
      form: {
        username: '11',
        password: ''
      },
      showDel: false
    }
  },
  methods: {
    logon() {
      console.log('login')
    },
    onInput(e) {
      let value = e.detail.value
      if(value) {
        this.showDel = true
        return
      }
      this.showDel = false
    },
    clickAction(type) {
      uni.navigateTo({
        url: 'register?type=' + type
      })
    },
    goLogin() {
      uni.switchTab({
        url: '../index/index'
      })
    }
  }
}
